class PropertyItem {
  constructor (id, key = '', title = '', description = '', value = '', options = {}) {
    console.log('this.count = ', this.count)
    this.id = id   // ID
    this.key = key // Key
    this.title = title // 
    this.description = description // 
    this.value = value // 
    this.readonly = options.readonly || false  // 
    this.extend = options.extend || {}         // 
  }
}

